.about {
  padding: 48px;
}

.about-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10vw;
}

.about-img {
  height: 360px;
}

.about-info {
  font-size: 24px;
}

.info-name {
  font-size: 28px;
  font-weight: 800;
}
.about-bottom {
  padding-top: 48px;
}
@media only screen and (max-width: 720px) {
  .about {
    padding: 24px;
  }

  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .about-img {
    height: 150px;
  }

  .about-info {
    font-size: 20px;
  }

  .info-name {
    font-size: 24px;
    font-weight: 700;
  }

  .about-bottom {
    padding-top: 24px;
  }
}
