.contact {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.contact-container {
  display: flex;
  flex: 1;
  justify-content: space-between;
  align-items: center;
  margin: 16px 0px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contact-left {
  max-width: 60%;
  font-size: 20px;
  text-align: center;
}

.download {
  background-color: grey;
  padding: 8px 16px;
  border-radius: 4px;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.download a {
  text-decoration: none;
  color: white;
}

.icon {
  align-items: center;
  margin-right: 6px;
}

@media only screen and (max-width: 720px) {
  .contact-left {
    max-width: 100%;
  }
  .download {
    font-size: 20px;
  }
}
