html {
  scroll-behavior: smooth;
}

.home {
  display: flex;
  flex-direction: column;
  width: 70%;
  margin: auto;
}

@media only screen and (max-width: 1024px) {
  .home {
    width: 90%;
  }
}
@media only screen and (max-width: 512px) {
  .home {
    width: 98%;
  }
}
.section-title {
  font-size: 36px;
  font-weight: 800;
}
@media only screen and (max-width: 720px) {
  .section-title {
    font-size: 28px;
    font-weight: 700;
  }
}
