.social-contact {
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-icon-div {
  padding: 6px;
  cursor: pointer;
  margin: 6px;
}

.social-icon {
  color: black;
  font-size: 48px;
}

@media only screen and (max-width: 720px) {
  .social-icon {
    font-size: 36px;
  }
}
